
import FileSaver from "file-saver";
import { defineComponent } from "vue";
import { mapState } from "vuex";

export default defineComponent({
  name: "MenuOptions",
  data() {
    return {
      clickedFile: false,
      clickedEdit: false,
    };
  },
  computed: mapState({
    nodeList: (state: any) => state.nodeList,
  }),
  methods: {
    toggleFile() {
      this.clickedFile = !this.clickedFile;
      if (this.clickedFile) {
        this.clickedEdit = false;
      }
    },
    saveFile() {
      const temp: any = [];
      this.nodeList.forEach((el: any) => {
        temp.push(el);
      });
      var blob = new Blob([JSON.stringify(temp)], {
        type: "text/plain;charset=utf-8",
      });
      FileSaver.saveAs(blob, "myflow.aim");
    },
    toggleEdit() {
      this.clickedEdit = !this.clickedEdit;
      if (this.clickedEdit) {
        this.clickedFile = false;
      }
    },
  },
});
